.sendForm {
    display: flex;
   align-items: center;
    margin-top: 20px;
    margin-left: 20px;


    width: 960px;
    height: 40px;

    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
}

.textarea {
    width: 414px;
    height: 60px;
    margin-left: 15px;

    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
}

.button {
    width: 120px;
    height: 40px;
    margin-left: 20px;
    margin-top: 20px;

    background: #0066CC;
    border-radius: 20px;
    color: white
}

.button:hover {
background-color: violet;
}
.button:active {
background-color: green;
}
