.friendMessage {
}

.friendImageAndText {
   display: flex;
   align-items: center;
   }

.friendImageAndText img {

   width: 48px;
   height: 48px;

   margin-right: 15px;
   margin-left: 20px;
}

.friendText {
   display: flex;
   flex-direction: column;
   justify-content: center;
   width: 414px;
   height: 60px;
   padding:5px;

   background: #F5F7FB;
   box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
}

.friendText:after {

}

.friendName {
   width: 200px;
   height: 20px;

   font-family: 'Montserrat';
   font-style: normal;
   font-weight: 600;
   font-size: 16px;
   line-height: 20px;

   color: #000000;
}

.friendMessageText {

   width: 374px;
   height: 20px;

   font-family: 'Montserrat';
   font-style: normal;
   font-weight: 400;
   font-size: 16px;
   line-height: 20px;

   color: #000000;
}

.friendTime {
   width: 29px;
   height: 12px;
   margin-left: 30px;

   font-family: 'Montserrat';
   font-style: normal;
   font-weight: 600;
   font-size: 10px;
   line-height: 12px;

   color: #000000;

}
